body {
  margin: 0;
  padding: 0;
  background-color: #2f2f2f;
  color: #ecf0f1;
  font-family: PT Sans, "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  height: 100%;
}
html,
body,
#root,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  color: #fff;
  font-size: 40px;
  font-family: PT Sans, "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 100;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
  /* text-align: center;
  margin-top: 200px; */
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.loader__sub {
  color: #fff;
  font-size: 20px;
  font-family: PT Sans, "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 100;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
}

